<template>
  <el-card class="box-card" shadow="never">
    <div slot="header">
      <span v-if="token != null && token !== ''">💐 签到</span>
      <span v-else>💐 登录</span>
    </div>
    <div v-if="token != null && token !== ''" class="has-text-centered">
      <b-button v-if="flag||user.sign" type="is-danger" inlined>📝 已签到</b-button>
      <b-button v-else type="is-danger" @click="signIn" outlined>📝 签到</b-button>
      <router-link v-if="token != null && token !== ''" to="/post/create">
        <b-button style="margin-left: 10px" type="is-danger" outlined>📝发帖</b-button>
      </router-link>
    </div>

    <div v-else class="has-text-centered">
      <b-button type="is-primary" tag="router-link" :to="{path:'/register'}" outlined>马上入驻</b-button>
      <b-button type="is-danger" tag="router-link" :to="{path:'/login'}" outlined class="ml-2"> 社区登入</b-button>
    </div>
  </el-card>
</template>

<script>
import { mapGetters } from 'vuex'
import {signIn} from "../../api/user";

export default {
  name: 'LoginWelcome',
  data(){
    return{
      flag:false
    }
  },
  computed: {
    ...mapGetters([
      'token','user'
    ])
  },
  methods:{
    signIn(){
      signIn().then((res)=>{
        this.flag=res.data.sign
        this.$message({
          message: "签到成功",
          type: "success",
          duration: 2000,
        });
      })
    }
  }
}
</script>

